import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild
} from '@angular/core';
import {
  AlarmsDeviceGridColumn,
  DeviceGridComponent,
  GroupDeviceGridColumn,
  ModelDeviceGridColumn,
  NameDeviceGridColumn,
  RegistrationDateDeviceGridColumn,
  StatusDeviceGridColumn,
  TypeDeviceGridColumn
} from '@c8y/ngx-components/device-grid';
import { Column, FilteringActionType } from '@c8y/ngx-components';
import { Observable, Subject } from 'rxjs';
import { distinctUntilChanged, takeUntil } from 'rxjs/operators';

@Component({
  selector: 'c8y-device-selector',
  templateUrl: 'device-selector.component.html'
})
export class DeviceSelectorComponent implements OnInit, OnDestroy {
  @ViewChild(DeviceGridComponent, { static: true })
  deviceGrid: DeviceGridComponent;

  @Input()
  deviceTypes: Observable<string[]>;

  typeColumn: Column = new TypeDeviceGridColumn();

  @Output()
  onDeviceQueryStringChange: EventEmitter<string> = new EventEmitter<string>();

  columns: Column[] = [
    new StatusDeviceGridColumn(),
    new NameDeviceGridColumn(),
    this.typeColumn,
    new ModelDeviceGridColumn(),
    new GroupDeviceGridColumn(),
    new RegistrationDateDeviceGridColumn(),
    new AlarmsDeviceGridColumn()
  ];

  private unsubscribe$: Subject<boolean> = new Subject();

  ngOnInit(): void {
    if (this.deviceTypes) {
      this.deviceTypes
        .pipe(
          distinctUntilChanged(),
          takeUntil(this.unsubscribe$)
        )
        .subscribe((types: string[]) => {
          this.deviceGrid.updateFiltering([this.typeColumn.name], {
            type:
              types && types.length
                ? FilteringActionType.ApplyFilter
                : FilteringActionType.ResetFilter,
            payload: types
              ? {
                  filteringModifier: {
                    externalFilterQuery: {
                      types
                    }
                  }
                }
              : undefined
          });
        });
    }
  }

  ngOnDestroy(): void {
    this.unsubscribe$.next(true);
    this.unsubscribe$.complete();
  }
}
